import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AddChemistComponent } from './add-chemist.component';
import { AddChemistRoutingModule } from './add-chemist-routing.module';
import { SharedModule } from '../../../shared/shared.module';
import { ChartModule } from 'angular2-chartjs';
import { SimpleNotificationsModule } from 'angular2-notifications';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import '../../../../assets/jquery-locationpicker-plugin-master/src/locationpicker.jquery.js';

@NgModule({
  imports: [
    CommonModule,
    AddChemistRoutingModule,
    SharedModule,
    ChartModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    SimpleNotificationsModule.forRoot()
  ],
  declarations: [AddChemistComponent],
  bootstrap: [AddChemistComponent]
})
export class AddChemistModule { }



