import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';

import { GetChemistDataService } from '../../services/GetChemistDataService';
import { AddAreaService } from '../../services/AddAreaService';
import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { AddchemistService } from '../../services/AddChemistService';

import { ValidationModel } from './../../models/validation.class';
import { Location } from '@angular/common';

declare var jQuery: any;

@Component({
  selector: 'add-chemist',
  templateUrl: './add-chemist.component.html',
  styleUrls: [
    './add-chemist.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [GetChemistDataService, AddAreaService, GetPopularChemistService, AddchemistService]
})
export class AddChemistComponent implements OnInit {
  public FullName: string = "";
  public Address: string = "";
  public Email: string = "";
  public PharmaCommission: number = 0;
  public City: number = 0;
  public Area: number = 0;
  public PharmacistId: number = 0;
  // public Username: string = "";
  // public Password: string = "";
  public Latitude: number = 0;
  public Longitude: number = 0;

  public cities_array = [];
  public areas_array = [];
  public chemists_array = [];
  public CityName: any;
  public AreaName: any;
  public chemistData = {
    Email: '',
    Chemist: {
      PharmacyName:'',
      Address: '',
      ContactPersonName: '',
      PharmaCommission: 0,
      City: {
        Id: '',
        Name: ''
      },
      Area: {
        City: {
          Id: '',
          Name: ''
        }

      }
    }
  }
  public runner: boolean = false;

  constructor(public _getChemistDataService: GetChemistDataService,
    public _getPopularChemistService: GetPopularChemistService,
    public _AddAreaNameService: AddAreaService,
    public _addChemistServiceService: AddchemistService) {
    this.getAllCities();
    this.getChemists('');
  }
  buildMap(lat, lng) {
    var self = this;
    jQuery('#somecomponent').locationpicker({
      location: {
        latitude: +lat || 25.0700,
        longitude: +lng || 67.2848
      },
      radius: 300,
      inputBinding: {
        latitudeInput: self.Latitude,
        longitudeInput: self.Longitude,
        radiusInput: jQuery('#us7-radius'),
        locationNameInput: jQuery('#us7-address'),
      },
      onchanged: function (currentLocation, radius, isMarkerDropped) {
        console.log(currentLocation);  //latlon  
        self.Latitude = currentLocation.latitude;
        self.Longitude = currentLocation.longitude;
      },
      enableAutocomplete: true,
      resize: true,
      autosize: true
    });
    self.Latitude = lat;
    self.Longitude = lng;
  }

  ngOnInit(): void {
    var self = this;
    jQuery('#somecomponent').html("Loading Map");
    navigator.geolocation.getCurrentPosition(showPosition);
    function showPosition(position) {
      var lat = position.coords.latitude;
      var lng = position.coords.longitude;
      $('.map-lat').val(lat);
      $('.map-lon').val(lng);
      self.buildMap(lat, lng);
    }
  }
  getAllCities() {
    this._getChemistDataService.getAllCitiesService().subscribe(
      response => {
        this.cities_array = response.data;
      });
  }// End of Get All Cities
  getCityAreas() {
   
      this._AddAreaNameService.getAreaName(this.City).subscribe(
        response => {

          this.areas_array = response.data;
        });
    

  }// End of Get Areas of City
  getChemists(id) {
    this.chemists_array = [];
    this._getChemistDataService.getChemistService().subscribe(
      response => {

        var abc = response.data.find(x => x.Chemist.Id == id);
        if (abc) {
          this.chemistData = abc;
          console.log(this.chemistData)
        }
        for (var i = 0; i < response.data.length; i++) {
          this.chemists_array.push(response.data[i].Chemist);
        }

      });

    console.log(this.chemistData);


  }// End of Get Chemists
  addChemist() {
    if (this.runner == false) {
      $('#loader').show();
      $('.text-danger').hide();
      this.runner = true;
     
      if (ValidationModel.validateString(this.chemistData.Chemist.ContactPersonName)) {
        this.runner = false;
        $('#loader').hide();
        $('#full_name_id_error').show();
        return;
      }
      if (ValidationModel.validateString(this.chemistData.Chemist.Address)) {
        this.runner = false;
        $('#loader').hide();
        $('#address_id_error').show();
        return;
      }
      if (ValidationModel.validateEmail(this.chemistData.Email)) {
        this.runner = false;
        $('#loader').hide();
        $('#email_invalid_id_error').show();
        return;
      }
      if (ValidationModel.validateString(this.chemistData.Email)) {
        this.runner = false;
        $('#loader').hide();
        $('#email_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.chemistData.Chemist.PharmaCommission)) {
        this.runner = false;
        $('#loader').hide();
        $('#pharma_commission_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.City)) {
        this.runner = false;
        $('#loader').hide();
        $('#city_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.Area)) {
        this.runner = false;
        $('#loader').hide();
        $('#area_id_error').show();
        return;
      }
      // if (ValidationModel.validateString(this.Username)) {
      //   this.runner = false;
      //   $('#loader').hide();
      //   $('#username_id_error').show();
      //   return;
      // }
      // if (ValidationModel.validateString(this.Password)) {
      //   this.runner = false;
      //   $('#loader').hide();
      //   $('#password_id_error').show();
      //   return;
      // }
      if (ValidationModel.validateNumber(this.Latitude)) {
        this.runner = false;
        $('#loader').hide();
        $('#latitude_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.Longitude)) {
        this.runner = false;
        $('#loader').hide();
        $('#longitude_id_error').show();
        return;
      }
      //after full this.password is passing in service
      this._addChemistServiceService.addChemist(this.Area, this.chemistData.Email, this.chemistData.Chemist.PharmaCommission, this.City, this.chemistData.Chemist.ContactPersonName, this.chemistData.Chemist.Address, this.Longitude, this.Latitude, this.PharmacistId, this.chemistData.Chemist.PharmacyName).subscribe(
        response => {
          $('#loader').hide();
          this.runner = false;
          console.log(response);
          this.chemistData.Chemist.ContactPersonName = "";
          this.chemistData.Chemist.Address = "";
          this.chemistData.Email= "";
          this.chemistData.Chemist.PharmaCommission = 0;
          this.City = 0;
          this.Area = 0;
          this.chemistData.Chemist.PharmacyName = "";
          this.PharmacistId = 0;
          // this.Username = "";
          // this.Password = "";
          this.Latitude = 0;
          this.Longitude = 0;
        }
      );
    }
  }//End of Add Chemist
}




